'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { useAuth } from '@/contexts/AuthContext'
import { useCourse } from '@/contexts/CourseContext'

interface StudentData {
  id: string
  name: string
  email: string
  progress: number
  completedLessons: number
  totalLessons: number
  enrolledAt: string
  lastAccessed: string
}

export default function DashboardProfessorPage() {
  const { user, logout } = useAuth()
  const { course, getAllStudents } = useCourse()
  const router = useRouter()
  const [students, setStudents] = useState<StudentData[]>([])

  useEffect(() => {
    if (!user) {
      router.push('/login')
      return
    }

    if (user.type !== 'teacher') {
      router.push('/dashboard/aluno')
      return
    }

    // Carregar dados dos alunos
    loadStudentsData()
  }, [user, router])

  const loadStudentsData = () => {
    const allProgress = getAllStudents()
    const users = JSON.parse(localStorage.getItem('fotomaster_users') || '[]')
    
    const studentsData: StudentData[] = allProgress.map(progress => {
      const student = users.find((u: any) => u.id === progress.userId)
      return {
        id: progress.userId,
        name: student?.name || 'Usuário não encontrado',
        email: student?.email || '',
        progress: Math.round(progress.progressPercentage),
        completedLessons: progress.completedLessons.length,
        totalLessons: course?.totalLessons || 0,
        enrolledAt: new Date(progress.enrolledAt).toLocaleDateString('pt-BR'),
        lastAccessed: new Date(progress.lastAccessed).toLocaleDateString('pt-BR')
      }
    })

    setStudents(studentsData)
  }

  const generateCertificate = (studentId: string, studentName: string) => {
    // Simular geração de certificado
    const certificateData = {
      studentId,
      studentName,
      courseName: course?.title || 'Curso de Fotografia',
      completionDate: new Date().toLocaleDateString('pt-BR'),
      instructor: user?.name || 'Professor',
      certificateId: `CERT-${Date.now()}`
    }

    // Salvar certificado
    const certificates = JSON.parse(localStorage.getItem('fotomaster_certificates') || '[]')
    certificates.push(certificateData)
    localStorage.setItem('fotomaster_certificates', JSON.stringify(certificates))

    alert(`Certificado gerado para ${studentName}!\nID: ${certificateData.certificateId}`)
  }

  if (!user) {
    return <div>Carregando...</div>
  }

  const totalStudents = students.length
  const completedStudents = students.filter(s => s.progress >= 100).length
  const averageProgress = students.length > 0 
    ? Math.round(students.reduce((acc, s) => acc + s.progress, 0) / students.length)
    : 0

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900">
      {/* Header */}
      <header className="bg-slate-800/50 backdrop-blur-sm border-b border-slate-700">
        <div className="container mx-auto px-4 py-4">
          <nav className="flex items-center justify-between">
            <Link href="/" className="flex items-center space-x-2">
              <div className="w-8 h-8 bg-white rounded-lg flex items-center justify-center">
                <span className="text-slate-900 font-bold text-sm">📷</span>
              </div>
              <span className="text-white font-bold text-xl">FotoMaster</span>
            </Link>
            
            <div className="flex items-center space-x-4">
              <span className="text-white">Olá, Prof. {user.name}</span>
              <Button 
                onClick={logout}
                variant="outline" 
                className="border-slate-600 text-slate-300 hover:bg-slate-700/50"
              >
                Sair
              </Button>
            </div>
          </nav>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-white mb-2">Dashboard do Professor</h1>
          <p className="text-slate-300">Gerencie seus alunos e acompanhe o progresso do curso</p>
        </div>

        {/* Cards de Estatísticas */}
        <div className="grid md:grid-cols-4 gap-6 mb-8">
          <Card className="bg-slate-800/50 border-slate-700 backdrop-blur-sm">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-slate-400 text-sm">Total de Alunos</p>
                  <p className="text-2xl font-bold text-white">{totalStudents}</p>
                </div>
                <div className="text-3xl">👥</div>
              </div>
            </CardContent>
          </Card>

          <Card className="bg-slate-800/50 border-slate-700 backdrop-blur-sm">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-slate-400 text-sm">Concluíram</p>
                  <p className="text-2xl font-bold text-green-400">{completedStudents}</p>
                </div>
                <div className="text-3xl">🎓</div>
              </div>
            </CardContent>
          </Card>

          <Card className="bg-slate-800/50 border-slate-700 backdrop-blur-sm">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-slate-400 text-sm">Progresso Médio</p>
                  <p className="text-2xl font-bold text-orange-400">{averageProgress}%</p>
                </div>
                <div className="text-3xl">📊</div>
              </div>
            </CardContent>
          </Card>

          <Card className="bg-slate-800/50 border-slate-700 backdrop-blur-sm">
            <CardContent className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-slate-400 text-sm">Total de Aulas</p>
                  <p className="text-2xl font-bold text-blue-400">{course?.totalLessons || 0}</p>
                </div>
                <div className="text-3xl">📹</div>
              </div>
            </CardContent>
          </Card>
        </div>

        <Tabs defaultValue="alunos" className="space-y-6">
          <TabsList className="bg-slate-800/50 border-slate-700">
            <TabsTrigger value="alunos" className="data-[state=active]:bg-slate-700">
              Gerenciar Alunos
            </TabsTrigger>
            <TabsTrigger value="curso" className="data-[state=active]:bg-slate-700">
              Conteúdo do Curso
            </TabsTrigger>
            <TabsTrigger value="certificados" className="data-[state=active]:bg-slate-700">
              Certificados
            </TabsTrigger>
          </TabsList>

          {/* Aba Alunos */}
          <TabsContent value="alunos" className="space-y-6">
            <Card className="bg-slate-800/50 border-slate-700 backdrop-blur-sm">
              <CardHeader>
                <CardTitle className="text-white">Lista de Alunos</CardTitle>
                <CardDescription className="text-slate-300">
                  Acompanhe o progresso de cada aluno inscrito no curso
                </CardDescription>
              </CardHeader>
              <CardContent>
                {students.length === 0 ? (
                  <div className="text-center py-8">
                    <p className="text-slate-400">Nenhum aluno inscrito ainda</p>
                  </div>
                ) : (
                  <div className="overflow-x-auto">
                    <Table>
                      <TableHeader>
                        <TableRow className="border-slate-700">
                          <TableHead className="text-slate-300">Nome</TableHead>
                          <TableHead className="text-slate-300">E-mail</TableHead>
                          <TableHead className="text-slate-300">Progresso</TableHead>
                          <TableHead className="text-slate-300">Aulas</TableHead>
                          <TableHead className="text-slate-300">Inscrição</TableHead>
                          <TableHead className="text-slate-300">Último Acesso</TableHead>
                          <TableHead className="text-slate-300">Ações</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {students.map((student) => (
                          <TableRow key={student.id} className="border-slate-700">
                            <TableCell className="text-white font-medium">
                              {student.name}
                            </TableCell>
                            <TableCell className="text-slate-300">
                              {student.email}
                            </TableCell>
                            <TableCell>
                              <div className="space-y-2">
                                <div className="flex items-center justify-between">
                                  <span className="text-white text-sm">{student.progress}%</span>
                                </div>
                                <Progress value={student.progress} className="h-2" />
                              </div>
                            </TableCell>
                            <TableCell className="text-slate-300">
                              {student.completedLessons}/{student.totalLessons}
                            </TableCell>
                            <TableCell className="text-slate-300">
                              {student.enrolledAt}
                            </TableCell>
                            <TableCell className="text-slate-300">
                              {student.lastAccessed}
                            </TableCell>
                            <TableCell>
                              <div className="flex space-x-2">
                                {student.progress >= 100 && (
                                  <Button
                                    size="sm"
                                    onClick={() => generateCertificate(student.id, student.name)}
                                    className="bg-green-500 hover:bg-green-600"
                                  >
                                    Certificado
                                  </Button>
                                )}
                                <Badge 
                                  variant={student.progress >= 100 ? "default" : "secondary"}
                                  className={student.progress >= 100 ? "bg-green-500" : ""}
                                >
                                  {student.progress >= 100 ? "Concluído" : "Em Progresso"}
                                </Badge>
                              </div>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          {/* Aba Curso */}
          <TabsContent value="curso" className="space-y-6">
            <Card className="bg-slate-800/50 border-slate-700 backdrop-blur-sm">
              <CardHeader>
                <CardTitle className="text-white">Estrutura do Curso</CardTitle>
                <CardDescription className="text-slate-300">
                  Visão geral dos módulos e aulas do curso
                </CardDescription>
              </CardHeader>
              <CardContent>
                {course?.modules.map((module, moduleIndex) => (
                  <div key={module.id} className="mb-6 last:mb-0">
                    <div className="flex items-center space-x-3 mb-4">
                      <div className="w-8 h-8 bg-orange-500 rounded-full flex items-center justify-center text-white font-bold">
                        {moduleIndex + 1}
                      </div>
                      <div>
                        <h3 className="text-lg font-semibold text-white">{module.title}</h3>
                        <p className="text-slate-400 text-sm">{module.description}</p>
                      </div>
                    </div>
                    
                    <div className="ml-11 space-y-2">
                      {module.lessons.map((lesson, lessonIndex) => (
                        <div 
                          key={lesson.id}
                          className="flex items-center justify-between p-3 bg-slate-700/30 rounded-lg border border-slate-600"
                        >
                          <div className="flex items-center space-x-3">
                            <span className="text-slate-400 text-sm">
                              {moduleIndex + 1}.{lessonIndex + 1}
                            </span>
                            <div>
                              <h4 className="text-white font-medium">{lesson.title}</h4>
                              <p className="text-slate-400 text-sm">{lesson.description}</p>
                            </div>
                          </div>
                          <div className="flex items-center space-x-2">
                            <Badge variant="secondary">{lesson.duration} min</Badge>
                            <Badge variant="outline">{lesson.materials.length} materiais</Badge>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                ))}
              </CardContent>
            </Card>
          </TabsContent>

          {/* Aba Certificados */}
          <TabsContent value="certificados" className="space-y-6">
            <Card className="bg-slate-800/50 border-slate-700 backdrop-blur-sm">
              <CardHeader>
                <CardTitle className="text-white">Gerenciar Certificados</CardTitle>
                <CardDescription className="text-slate-300">
                  Emita e gerencie certificados para alunos que concluíram o curso
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="grid gap-4">
                    {students.filter(s => s.progress >= 100).length === 0 ? (
                      <div className="text-center py-8">
                        <p className="text-slate-400">Nenhum aluno concluiu o curso ainda</p>
                      </div>
                    ) : (
                      students
                        .filter(s => s.progress >= 100)
                        .map((student) => (
                          <div 
                            key={student.id}
                            className="flex items-center justify-between p-4 border border-slate-600 rounded-lg"
                          >
                            <div>
                              <h4 className="text-white font-medium">{student.name}</h4>
                              <p className="text-slate-400 text-sm">
                                Concluído em {student.lastAccessed}
                              </p>
                            </div>
                            <Button
                              onClick={() => generateCertificate(student.id, student.name)}
                              className="bg-green-500 hover:bg-green-600"
                            >
                              Gerar Certificado
                            </Button>
                          </div>
                        ))
                    )}
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </main>
    </div>
  )
}
